import React from 'react'
import { Route,Switch, Redirect } from 'react-router-dom'
import Home from '../views/index';
import Login from '../views/login';
import News from '../views/news';
import Settings from '../views/settings';
import Subscription from '../views/subscription';
import Blogdetails from '../views/blog-details';

const Routes = () => (
	<Switch>
      <Route path="/react/market-squawk/" exact>
        <Home />
      </Route>
      <Route path="/react/market-squawk/login" exact>
        <Login />
      </Route>
      <Route path="/react/market-squawk/news" exact>
        <News />
      </Route>
      <Route path="/react/market-squawk/settings" exact>
        <Settings />
      </Route>
      <Route path="/react/market-squawk/subscription" exact>
        <Subscription />
      </Route>
      <Route path="/react/market-squawk/blog-details" exact>
        <Blogdetails />
      </Route>
    </Switch>
)

export default Routes